* {
  box-sizing: border-box;
}

body {
  background-image: url("../../img.3ff6cca6.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

.blok-title {
  padding-left: 25px;
}

.search-box {
  z-index: 100;
  width: 100%;
  justify-content: center;
  padding: 15px;
  display: flex;
  position: fixed;
  top: 50px;
  left: 0;
}

.search-form {
  justify-content: center;
  display: flex;
}

.input-search {
  width: 500px;
  letter-spacing: 1px;
  background-color: #7fd4ff;
  border-width: 3px;
  border-color: #00f;
  border-radius: 26px;
  outline: none;
  margin-right: 15px;
  padding: 4px 16px;
  font-size: 20px;
  font-style: italic;
}

.input-search:focus {
  color: #ffebcd;
  background-color: #0a7bdd;
  border: 3px solid #07e625ea;
}

.data-btn {
  height: 50px;
  width: 150px;
  color: #000;
  cursor: pointer;
  background-color: #7fd4ff;
  border-color: #00f;
  border-radius: 20px;
  outline: none;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-style: italic;
  transition: background-color .3s linear, color .3s linear;
  display: flex;
}

.data-btn:hover {
  color: #ffebcd;
  background-color: #0a7bdd;
  border-color: #07e625ea;
  transition: background-color .3s linear, color .3s linear;
}

.search-btn:disabled {
  color: #6c7277;
  cursor: none;
  background-color: #0a7bdd;
  border-color: #07e625ea;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 85px;
  margin-bottom: 20px;
  display: flex;
}

.photo-card {
  max-width: 300px;
  flex-direction: column;
  justify-content: center;
  transition: all .4s ease-in-out;
  display: flex;
}

.photo-card:focus, .photo-card:hover {
  cursor: pointer;
  scale: 1.05;
}

.photo-imag {
  width: 300px;
  height: 200px;
}

.info {
  background-color: #deed88;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  justify-content: space-around;
  padding: 5px;
  display: flex;
}

.info__box {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  font-size: 10px;
  display: flex;
}

.load-more {
  margin: 0 auto 20px;
}

.load-more.is-hidden {
  display: none;
}

.loader {
  width: 50px;
  margin: 0 auto;
}

.loader.hidden {
  visibility: hidden;
}

.material-icons--search {
  margin-right: 15px;
}

.btn-up-top:hover {
  color: #c3c3c3;
}

.btn-up-top {
  z-index: 1000;
  width: 50px;
  height: 50px;
  color: #000;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  background: #fdfdfd;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 3vh;
  right: .1vw;
  box-shadow: 0 1px 2px #3c40434d, 0 2px 6px 2px #3c404326;
}

.btn-up-top--visible {
  opacity: 1;
  visibility: visible;
}

